$blue: #3b74ad;
$black: #222222;

body, #root{
    margin: 0;
    padding: 0;
    height: 100vh;
    font-family: Arial, Helvetica, sans-serif;
}

.schedule{
    // max-height: 80vh;
    h1{
        margin-top: 0;
    }
    // margin-bottom: 20vh;

    .day{
        margin-bottom: 10vh;
    }
}

.logo{
    width: 100%;
    margin: auto;
    display: block;
}

.container{
    display: flex;
    flex-direction: column;
    height: 100%;
    .body{
        flex-grow: 1;
        padding: 5vw;
        text-align: justify;
    }
    .body--no-padding{
        padding: 0;
    }
    .buttons{
        display: flex;
        flex-direction: column;
        flex-shrink: 1;

        a{
            width: 100%;
        }
    }

    .buttons--inline{
        flex-direction: row;

        .button{
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}


a{
    text-decoration: none;
}

.button{
    display: block;
    width: 100%;
    padding: 6vw;
    border: none;
    font-size: 1em;
    text-transform: uppercase;
    margin: 0;
    flex-grow: 1;
}
.button--blue{
    background-color: $blue;
    color: white;

    &:active, &:focus{
        background-color: darken($blue,10);
        outline: 0;
    }
}
.button--black{
    background-color: $black;
    color: white;
    
    &:active, &:focus{
        background-color: darken($black,10);
        outline: 0;
    }
}

.appNav{
    width: 100%;
    background-color: $black;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    position: fixed;
    bottom: 0;
    left: 0;
    max-height: 10vh;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
    // box-shadow: 0px 0px 4px rgba(0,0,0,0.2);

    svg{
        width: 4vh !important;
        height: 4vh !important;
    }
    
    a{
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgb(65,65,65);
        height: 100%;
        flex-grow: 1;

        &:last-child{
            border: none;
        }
    }
}

.mapFrame{
    width: 100vw;
    height: 83vh;
    border: none;
}

.bridgeItem{
    position: relative;
    padding: 0;
    img{
        width: 100%;
        border-radius: 2px;
    }
    .bridgeViewImage{
        box-shadow: 0px 1px 4px rgba(0,0,0,0.4);
    }

    .bridgeDescription{
        color: #333;
    }

    margin-bottom: 5vw;

    .paddingContainer{
        height: auto;
        margin-bottom: 10vh;
        h1{
            font-weight: bold;
            font-size: 1.6em;
            margin: 0;
        }
    }

    .sponsorLogos{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 10vw;
        grid-row-gap: 5vw;
    }

    .sponsorLogoImg{
        display: flex;
        align-items: center;
    }
}

.bridgeItem--no-padding{
    margin-bottom: 0;
    padding-bottom: 0;
}

.paddingContainer{
    padding: 5vw;
}

.bridgeList{
    margin-bottom: 18vw;
}

.nav--active{
    background: #3b74ad;
    svg{
        color: white; 
    }
}

.bridgeIdLabel{
    position: absolute;
    top: 0;
    right: 0;
    width: 15vw;
    height: 15vw;
    background-color: rgba(59, 116, 173,.8);
    color: white;
    text-align: center;
    line-height: 15vw;
    font-size: 1.5em;
}

.userLocation{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2vh 1vh;
    background-color: rgb(52, 160, 52);
    // background: lighten($blue,20);
    color: white;
    font-size: 1em;
    height: 3vh;
    max-height: 3vh;
}

.day{
    h2{
        color: $blue;

        small{
            color: $black;
            font-weight: normal;
        }
    }
}

.geo--unsupported{
    background-color: rgb(201, 63, 63);
}